
.stf-select {
  height: 40px;
  position: relative;
  background-color: #F0F1F5;
  border-radius: 2px;
  box-sizing: border-box;
  margin: 0;
}
.stf-select__container {
    height: 100%;
    position: relative;
    box-sizing: border-box;
    cursor: text;
}
.stf-select__inner-wrapper {
    height: 100%;
    background-color: #F0F1F5;
    border-radius: 2px;
    padding: 0 9px;
}
.stf-select__value {
    display: none;
    height: 100%;
    font-size: 12px;
    font-weight: 700;
    line-height: 32px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    box-sizing: border-box;
}
.stf-select__placeholder {
    height: 100%;
    width: 100%;
    font-size: 12px;
    line-height: 40px;
    color: #888aa3;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    box-sizing: border-box;
    padding-top: 1px;
}
.stf-select__options {
    width: 100%;
    min-height: 0;
    position: absolute;
    top: 50px;
    background-color: #FFFFFF;
    border-radius: 0 0 2px 2px;
    visibility: hidden;
    z-index: -10;
}
.stf-select_sm {
  height: 32px;
}
.stf-select_sm .stf-select__placeholder {
    line-height: 32px;
}
.stf-select_sm .stf-select__value {
    line-height: 33px;
}
.stf-select-options {
  display: block;
  max-height: 200px;
  overflow-y: auto;
  overflow-x: hidden;
}
.stf-select-option {
  position: relative;
  font-size: 14px;
  line-height: 16px;
  padding: 12px 24px 12px 12px;
  border-bottom: 1px solid rgba(136, 138, 163, 0.3);
  box-sizing: border-box;
  cursor: pointer;
  outline: none;
}
.stf-select-option_selected {
    font-weight: 700;
}
.stf-select-option_selected:after {
      content: '';
      display: inline-block;
      width: 20px;
      height: 20px;
      background: url("./icn-done-cargo.svg") center no-repeat;
      position: absolute;
      top: 50%;
      right: 4px;
      margin-top: -10px;
}
.stf-select-option:hover, .stf-select-option:focus {
    background: rgba(135, 138, 163, 0.1);
}
.stf-select__search-input {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 2px;
  z-index: -1;
}
.stf-select__search-input input {
    visibility: hidden;
    background-color: #FFFFFF;
    border: none;
    border-radius: 2px;
    padding: 0 9px;
}
.stf-select__search-input:focus {
    outline: none;
}
.stf-select_has-value .stf-select__value {
  display: block;
}
.stf-select_has-value .stf-select__placeholder {
  display: none;
}
.stf-select_opened .stf-select__placeholder {
  display: none;
}
.stf-select_opened .stf-select__options {
  border-top: 1px solid rgba(136, 138, 163, 0.3);
  visibility: visible;
  z-index: 9;
}
.stf-select_opened .stf-select__search-input {
  z-index: 1;
}
.stf-select_opened .stf-select__search-input input {
    visibility: visible;
    border-radius: 2px 2px 0 0;
    height: 32px;
    font-size: 12px;
    padding: 0 8px;
    width: 100%;
}
.stf-select_opened.stf-select_empty .stf-select__options {
  display: none;
}
.stf-select_opened.stf-select_empty .stf-select__search-input input {
  border-radius: 2px;
}



